// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { themeStretch } = useSettings();
  const { translate } = useLocales();

  return (
    <Page title="General: App">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <HomeWelcome displayName={translate('generalApp.hello')} />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>
        </Grid>
        <HomeSectionHeader
          heading={translate(`other.Development progress`)}
          action={
            <ButtonPopover
              button={translate(`other.Your Proposal`)}
              popover={translate(`other.Under Development`)}
              icon={<Icon icon={plusFill} />}
            />
          }
        />
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <HomeWorkProgress
              title={translate(`generalApp.Decentralization SwitchSwap`)}
              tasks={DECENTRALIZATION_TASKS}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title={translate(`generalApp.Smart Contracts Dev`)} tasks={SMARTCONTRACT_TASKS} />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title={translate(`generalApp.Interface Design`)} tasks={INTERFACEDESIGN_TASKS} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
