import { useState } from 'react';
import PropTypes from 'prop-types';

// material
import { Box, Button, Popover, Typography } from '@material-ui/core';

ButtonPopover.propTypes = {
  button: PropTypes.string,
  popover: PropTypes.string,
  icon: PropTypes.element
};

export default function ButtonPopover({ button, popover, icon }) {
  const [clickButton, setCLickButton] = useState(null);

  const handleClickButton = (event) => {
    setCLickButton(event.currentTarget);
  };

  const handleCloseButton = () => {
    setCLickButton(null);
  };
  return (
    <>
      <Button onClick={handleClickButton} variant="contained" startIcon={icon}>
        {button}
      </Button>
      <Popover
        open={Boolean(clickButton)}
        anchorEl={clickButton}
        onClose={handleCloseButton}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center'
        }}
      >
        <Box sx={{ p: 2, maxWidth: 280 }}>
          <Typography variant="body2" sx={{ color: 'text.secondary' }}>
            {popover}
          </Typography>
        </Box>
      </Popover>
    </>
  );
}
